
import Search from 'vux/src/components/search/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
name: 'cat_select',
components: {
Search,
Scroller
},
data() {
return {
currentFistCat: 0,
firstLevelCats: [],
list: {}
}
},
activated: function() {
var self = this;
self.getFirstLevel();
},
methods: {
selectSecCat: function(pid) {
var self = this;
self.currentFistCat = pid;
self.xarpost('MallCat/getCatList', {
'pid': pid,
}).then(function(d) {
let {
data,
error,
message
} = d;
if(d.error != 0) {
self.$vux.toast.show({
text: d.message,
type: 'cancel',
time: 3000
});
}

var newData = (d.data == undefined) ? [] : d.data;
self.setSecData(newData, pid);
}, function(e) {
self.$vux.toast.show({
text: e,
type: 'cancel',
time: 3000
});
});
},
getFirstLevel: function() {
var self = this;
self.xarpost('MallCat/getCatList', {
'pid': 0,
}).then(function(d) {
let {
data,
error,
message
} = d;
if(d.error != 0) {
self.$vux.toast.show({
text: d.message,
type: 'cancel',
time: 3000
});
}

var newData = (d.data == undefined) ? [] : d.data;
self.setFirstData(newData);
}, function(e) {
self.$vux.toast.show({
text: e,
type: 'cancel',
time: 3000
});
});
},
setFirstData: function(list) {
this.firstLevelCats = list;
},
setSecData: function(list, pid){
this.list['sec_'+pid] = list;
},
setFocus() {
this.$refs.search.setFocus()
},
onSubmit() {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
onFocus() {
console.log('on focus')
},
onCancel() {
console.log('on cancel')
}
}
}



